<template>
  <b-overlay :show="!propertyData.rootLocations" rounded="sm" :opacity="1">
    <ProgressCircle :current-step="1" :total-steps="7"></ProgressCircle>
    <div class="form-container">
      <h2 class="form-heading">{{ $t('form.firstStepHeading') }}</h2>
      <div class="form-input">
        <label> {{ $t('common.city') }} <sup style="color: red">*</sup> </label>

        <rent-select
          :options="propertyData.rootLocations"
          id="citySelect"
          @input="cityInput"
          :title="$t('form.select_city')"
          :iconName="require(`@/assets/svg-sprite/svg/city.svg`)"
          :placeholder="$t('form.select_city')"
          label="location_name"
          value="location_id"
          class="destination-input"
          :defaultValue="cityDefault" />
      </div>
      <transition name="slide-down-fade">
        <div
          class="form-input"
          v-if="property.form1.city && districtOptions.length">
          <label>
            {{ $t('common.district') }}
          </label>
          <rent-select
            :dir="dir"
            label="location_name"
            :options="districtOptions"
            @input="districtInput"
            v-if="property.form1.city !== null"
            :title="$t('form.select_district')"
            :iconName="require(`@/assets/svg-sprite/svg/district.svg`)"
            :placeholder="$t('form.select_district')"
            :defaultValue="districtDefault">
          </rent-select>
        </div>
      </transition>
      <transition name="slide-down-fade">
        <div
          class="form-input"
          v-if="
            property.form1.city &&
            property.form1.district &&
            districtOptions.length &&
            neighborhoodOptions.length
          ">
          <label>
            {{ $t('common.neighborhood') }}
          </label>
          <rent-select
            :dir="dir"
            label="location_name"
            :options="neighborhoodOptions"
            @input="neighborhoodInput"
            :title="$t('form.choose_neighborhood')"
            v-if="property.form1.district !== null"
            :iconName="require(`@/assets/svg-sprite/svg/neighborhood.svg`)"
            :placeholder="$t('form.choose_neighborhood')"
            :defaultValue="neighborhoodDefault">
          </rent-select>
        </div>
      </transition>
    </div>

    <div class="page-actions">
      <b-button variant="primary" @click="goNext">
        {{ $t('common.next') }}
      </b-button>
    </div>
  </b-overlay>
</template>

<script>
import rentSelect from '~/components/shared/form/rentSelect';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';

export default {
  name: 'AddPropertyLocation',
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    rentSelect,
    ProgressCircle,
  },
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  data() {
    return {
      property: {
        form1: {
          city: null,
          district: null,
          neighborhood: null,
        },
        form2: null,
        form3: null,
        form4: null,
        form5: null,
        form6: null,
      },
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Add Property | Step 1" : "إضافة عقار | الخطوة 1"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Add Property | Property address' : 'إضافة عقار | عنوان العقار'
        }
      ]
    }
  },
  computed: {
    appEnv(){
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    districtOptions() {
      return this.$store.state.PropertyModule.districtLocation;
    },
    neighborhoodOptions() {
      return this.$store.state.PropertyModule.neighborhoodLocation;
    },
    cityDefault() {
      return this.property?.form1?.city || '';
    },
    districtDefault() {
      return this.property?.form1?.district || '';
    },
    neighborhoodDefault() {
      if (!this.districtDefault) return '';
      return this.property?.form1?.neighborhood || '';
    },
  },
  watch: {
    async 'property.form1.city'() {
      await this.fetchLocations('district');
    },
    async 'property.form1.district'() {
      await this.fetchLocations('neighborhood');
    },
  },
  methods: {
    async fetchLocations(type) {
      const location_id =
        type === 'district'
          ? this.property.form1.city.location_id
          : this.property.form1.district.location_id;

      await this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id,
        type,
      });
    },
    cityInput(city) {
      if (!city.location_id) return;

      this.property.form1.city = city;

      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: city.location_id,
        type: 'district',
      });
      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: '',
        type: 'neighborhood',
      });
    },
    districtInput(district) {
      if (!district?.location_id) return;

      this.property.form1.district = district;
      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: district.location_id,
        type: 'neighborhood',
      });
    },
    neighborhoodInput(neighborhood) {
      this.property.form1.neighborhood = neighborhood;
    },
    goNext() {
      const { city } = this.property.form1;
      if (!city) {
        let body = this.$t('form.postingRequest.selectedDestination.fillCity');
        this.$bvToast.toast(body, {
          title: this.$t('form.postingRequest.selectedDestination.fillCity'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }
      localStorage.setItem('property', JSON.stringify(this.property));
      this.$router.push(this.localePath('/properties/add/2'));
    },
  },
  mounted() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    if (localStorage.getItem('property'))
      this.property = JSON.parse(localStorage.getItem('property'));
  },
};
</script>

<style>
.slide-down-fade-enter {
  transform: translateY(-10px);
  opacity: 0;
}

.slide-down-fade-enter-active,
.slide-down-fade-leave-active {
  transition: all 0.3s ease;
}

.slide-down-fade-leave-to {
  transform: translateY(10px);
  opacity: 0;
}
</style>
